import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

// import stocks from './modules/stocks'

const store = new Vuex.Store({
  state: {
    stocks: [
      { id: 1, name: "Kotak", price: 2000 },
      { id: 2, name: "Puma", price: 150 },
      { id: 3, name: "Parle-G", price: 850 },
      { id: 4, name: "Seven", price: 3000 },
      { id: 5, name: "Tanishq", price: 4500 },
      { id: 6, name: "Reebok", price: 140 },
      { id: 7, name: "Atlas", price: 90 },
      { id: 8, name: "Tata", price: 2300 },
    
    ],

    //for portfolio
    funds:10000,
    portfolio_Stocks:[]
  },
 
   mutations: {
      SET_STOCKS(state,stocks){
          state.stocks= stocks
      },
      RND_STOCKS(state){
          console.log(state);
     //randomized stock main
        state.stocks.forEach(stock=>{
            stock.price = Math.round(stock.price*(1+Math.random() -0.5));
            
        })
        //randomizing portfolio stocks
        state.portfolio_Stocks.forEach(stock=>{
            stock.price = Math.round(stock.price*(1+Math.random() -0.5));
            
        })
      },

      //for portfolio
      //here destructurng is used 2nd args 
      'BUY_STOCK'(state,order){
          var stockId= order.stockId;
          var stockName = order.stockName;
          var stockPrice= order.price;
          var quantity= order.quantity;
        
          if(state.funds<stockPrice*quantity){
              alert("not enough funds")
             return;
          }

    const found = state.portfolio_Stocks.find(item => item.id ===stockId);
    if(found){       
       let pastQuantity=  parseInt(found.quantity);
        let newQuantity= parseInt(quantity);
        found.quantity= pastQuantity + newQuantity;
        
    }else{
              state.portfolio_Stocks.push({
                  id:stockId,
                  name:stockName,
                  price:stockPrice,
                  quantity:quantity
              })
          }
          state.funds-= stockPrice*quantity;
      },
      "SELL_STOCK"(state,order){
         
          var stockId= order.stockId;
        //   var stockName = order.stockName;
          var stockPrice= order.stockPrice;
          var quantity= order.quantity;
          const found = state.portfolio_Stocks.find(item => item.id ===stockId);
          if(found.quantity<quantity){
             alert("Not enough quantity to sell")
          }else{
            found.quantity-=quantity;
            state.funds+= quantity*stockPrice;
            // if(found.quantity==0){
               
            // }
          }
    

      }
  },
  actions: {
   
    testActionBuyStocks(context,order){
        context.commit('BUY_STOCK',order)
    },
    // initStocks({commit},state){
    //     //first time set stocks as  data in data/stocks.js
    //     commit('SET_STOCKS',state.stocks)
    // },
    randomizeStocks({commit}){
      
        commit('RND_STOCKS');
    },

    //portfolio actions goes here
    sellStock(context,order){
         context.commit('SELL_STOCK',order)
    }
  },

  getters:{
    stocks(state){
        return state.stocks;
    },

    // not under stood this fun 10th video in vuex
    stock_Portfolio(state){
        return state.portfolio_Stocks.map(stock=>{
            const record= state.stocks.find(element=>{
                element.id==stock.id
            })
            return{
                id:stock.id,
                quantity:stock.quantity,
                name: record.name,
                price:record.price
            }
        })
    },

    myStocks(state){
        return state.portfolio_Stocks;
    },
    funds(state){
        return state.funds;
    }
   

},

//   modules:{
//       stocks
//   }
});

export default store;
