<template>
  <div>
    <app-Stock
      class="main-stock"
      v-for="stock in stocks"
      :stock="stock"
      :key="stock"
    >
    </app-Stock>
  </div>
</template>

<script>
// import { mapGetters } from "vuex";
import Stock from "./stock";

// import {mapGetters} from 'vuex'
    export default {
      computed: {
          stocks(){
              return this.$store.getters.myStocks;
          }
      },


  components: {
    appStock: Stock,
  },
};
</script>


<style  scoped>
.main-stock {
  display: inline;
}
</style>