<template>
  <div app-stock-container>
  
    <div class="main-content">
        <div class="badge">
      Purchsed
    </div>
      <div class="main-name-price">
        <div class="main-name">
          {{ stock.name }}
        </div>
        <div class="main-price">
         <small>Price</small> <i class="fa fa-inr" aria-hidden="true"></i>
         {{ stock.price }} 
        </div>
      </div>
      <div class="main-input">
        <input 
        type="number" 
        v-model="quantity" 
        placeholder="Quantity"
         />
      </div>
      <!-- floats are also working fix it -->
       <div class="main-buy-btn">
       <strong>Qunatity:{{ stock.quantity}} </strong>
      </div>
      <div class="main-buy-btn">
        <button @click="sellStock" :disabled="this.quantity<=0">Sell</button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  props: ["stock"],
  data() {
    return {
      quantity: null,
    };
  },
  methods: {
      sellStock(){
          const order={
              stockId:this.stock.id,
              stockName:this.stock.name,
              stockPrice: this.stock.price,
              quantity: this.quantity
          }
              this.$store.dispatch('sellStock',order)
      }
  },
};
</script>

<style scoped>
.main-content {
  border: 0.2px solid gray;
  box-shadow: 2px 4px 8px rgb(10, 10, 10);
  width: 200px;
  max-width: 350px;
background: linear-gradient(#c9f139,#5ee981);
  border-radius: 10px;
  overflow: hidden;
  display: inline-block;
}
.main-content .badge{
  position: absolute;
  transform: translateX(30px);
   transform: translateY(-20px);
   font-weight: bolder;
   font-style: italic;
   color: red;
}
.main-name-price {
  margin: 0.5rem;
}
.main-name {
  display: inline;
  font-size: 1.5rem;
  font-weight: bolder;
  color: rgb(1, 0, 3);
}
.main-price {
  display: inline;
  color: rgb(5, 17, 87);
  font-weight: bold;
}
.main-input {
  margin: 0.5rem;
}
.main-input input {
  font-size: 1.3rem;
  width: 90%;
  border-radius: 5px;
}
.main-input input:focus {
  outline: none;
}
.main-buy-btn {
  padding: 0.5rem;
}
.main-buy-btn button {
  width: 95%;
  cursor: pointer;
  font-size: 1.3rem;
  border-radius: 20px;
  background: rgb(229, 219, 248);
  border-block: 1px solid green;
}
.main-buy-btn button:focus {
  outline: none;
  background: rgb(0, 0, 0);
  color: rgb(248, 248, 248);
}
</style>
